.banner {
    margin-top: -10vh;
    height: 100vh;
    margin-bottom: -80px;
    color: white;
    object-fit: contain;
    position: relative;
    top: 0px;
    display: flex;
    align-items: center;
}

.banner__contents {
    margin-left: 30px;
    padding-top: 500px;
    /* height: 90px; */
    z-index: 2;
}

.banner__title {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
    padding-bottom: 1rem;
}

.banner__description {
    width: 45rem;
    line-height: 1;
    padding: 1rem 0;
    font-size: 1.1rem;
    max-width: 400px;
    height: 400px;
}
.banner__buttons{
    margin-top: 1rem;
}
.banner__button {
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-size: 1.2rem;
    font-weight: 700;
    border-radius: 0.2rem;
    padding: 0.7rem 3rem;
    margin-right: 1.5rem;
    background-color: rgba(51, 51, 51, 0.5);
}

.banner__button.play {
    color: #000;
    background-color: #fff;
}

.banner__button:hover {
    color: #000;
    background-color: #c6c6c6;
    transition: all 0.2s;
}

.banner__fadeBottom {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 40vh;
    background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 37, 0.61),
    #111
    );
}
